.navigation {
    width: fit-content;
    margin: auto;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 24px;
    @media (min-width: 992px) {
        height: 26px;
    }
    .swiper-pagination {
        width: fit-content !important;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
        position: relative;
        height: 100%;
        bottom: unset !important;
        @media (min-width: 992px) {
            gap: 50px;
        }
    }
    .swiper-pagination-bullet {
        width: 5px;
        height: 5px;
        background: #E9E9E9;
        opacity: 1;
        position: relative;
        transition: .4s;
        @media (min-width: 992px) {
            width: 6px;
            height: 6px;
        }
        &::after {
            content: '';
            position: absolute;
            left: -2px;
            top: -2px;
            transition: .4s;
            border-radius: 50%;
            width: 10px;
            height: 10px;
        }
        &.swiper-pagination-bullet-active {
            transition: .4s;
            background: transparent;
            &::after {
                border: 1px solid #000000;
                left: -2px;
                top: -2px;
                width: 10px;
                height: 10px;
            }
            
        }
    }
}
.navigation-container {
    position: relative;
    height: 100%;
    width: fit-content;
    margin: auto;
    padding: 0 50px;
    .navigation__button {
        position: absolute;
        width: 11px;
        top: 50%;
        transform: translateY(-50%);      
        height: 21px;
        cursor: pointer;
        @media (min-width: 992px) {
            width: 14px;    
            height: 26px;
        }
        svg {
            width: 100%;
            height: 100%;
        }
        &:hover {
            svg {
                fill: $blue;
            }
        }
        &-prev {
            left: 0;
        }
        &-next {
            right: 0;
            transform: scaleX(-1) translateY(-50%);
        }
        svg {
            transition: .4s;
            fill: #000000;
        }
    }
}