.form.form_mini {
    background: unset;
    border: 1px solid #EBEBEB;
    border-radius: 0 0 20px 20px;
    transform: translateY(-30px);
    border-top: unset;
    padding-right: 77px;
    .form__container {
        gap: 59px;
    }
    .form__content {
        .form__title {
            font-size: 30px;
        }
        &::after {
            background-image: unset;
        }
    }
}
.form__container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    position: relative;
    @media (min-width: 992px) {
        gap: 40px;
        display: grid;
        grid-template-columns: minmax(400px, 593px) 410px;
    }
    form {
        position: relative;
        padding-bottom: 40px;
        & > p {
            font-weight: 400;
            font-size: 12px;
            line-height: 110%;
            color: #9C9C9C;
            margin-top: 8px;
            margin-bottom: 25px;
            @media (min-width: 992px) {
                margin-bottom: 30px;
            }
        }
        button {
            margin-top: 15px;
            background: #0042A4;
            border-radius: 5px;
            width: 100%;
            padding: 17.5px;
            border: none;
            font-weight: 700;
            font-size: 16px;
            line-height: 100%;
            color: #FFF;
            margin-bottom: 22px;
            @media (min-width: 992px) {
                margin-top: 20px;
                font-size: 20px;
            }
        }
        .form__checkbox {
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
}
.form__content {
    padding-top: 52px;
    position: relative;
    @media (min-width: 992px) {
        padding-left: 77px;    
        padding-top: 0;
    }
